@import "./src/assets/themes/appdb/variables.scss";

.file {
  background: var(--tertiary-bg);
  border: 1px solid var(--tertiary-accent);
  border-radius: 2px;
  padding: 6px;
  margin-left: auto;
  flex: 0 0 150px;
  width: 150px;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.input {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  pointer-events: none;
}
