@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.main {
  display: block;
  flex-grow: 1;
}

.feature {
  display: flex;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #e5e5e5;

  &__info {
    display: block;
    font-size: 12px;
  }

  &__text {
    margin-right: 60px;
  }

  &__select,
  &__file {
    margin-left: auto;
  }

  &__input {
    background: var(--tertiary-bg);
    border: 1px solid var(--tertiary-accent);
    border-radius: 2px;
    padding: 6px;
    margin-left: auto;
    flex: 0 0 150px;
    width: 150px;
    font-size: 14px;
    font-family: "IBM Plex Sans", sans-serif;
    color: var(--text-primary);

    &::placeholder {
      font-family: "IBM Plex Sans", sans-serif;
      color: var(--text-primary);
    }
  }
}

.warning {
  color: var(--configure-error-text);
  background: var(--bg-error);
  border-radius: 2px;
  padding: 3px 10px;
  text-align: center;
  line-height: 18px;
  margin: 20px 0;

  @include notebook {
    margin: 10px 0;
  }
}

.heading {
  color: var(--text-tertiary);
  font-weight: 600;
  margin: 25px 0 15px;

  &:first-child {
    margin-top: 0;
  }

  @include notebook {
    margin: 15px 0 10px;
    line-height: 16px;
  }
}

.buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;

  @include tablet {
    flex-direction: column;
  }

  &_sa {
    justify-content: space-around;
  }

  &__delete,
  &__save {
    position: relative;
    padding: 5px 15px;
    font-size: 14px;
    border-radius: 2px;

    @include notebook {
      padding: 5px 8px;
    }

    @include tablet {
      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__delete {
    color: var(--text-primary);
    border: 1px solid rgba(250, 101, 101, 0.5);
  }

  &__save {
    background: $primary;
    color: #fff;
  }
}

.row {
  display: flex;
  align-items: flex-start;

  @include notebook {
    flex-direction: column-reverse;
  }
}

.features,
.desc {
  @include notebook {
    width: 100%;
  }
}

.desc {
  background: var(--secondary-bg);
  border-radius: 8px;
  flex: 0 0 200px;
  margin-left: 40px;
  padding: 10px;

  @include notebook {
    margin: 0 0 15px;
    flex: 0 0 auto;
  }

  &::v-deep {
    p:not(:last-child) {
      margin-bottom: 15px;
    }
  }
}

.inline-buttons {
  line-height: 33px;
  margin-top: -5px;

  .button {
    display: inline;
  }
}
